"use client"

import { useState, useEffect } from "react"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Alert, AlertDescription } from "@/components/ui/alert"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { Textarea } from "@/components/ui/textarea"
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table"

interface LahanData {
  id: string
  namaLahan: string
  luasLahan: number
  jenisTanah: string
  lokasi: string
  ketinggian: number
  kemiringan: string
  irigasi: string
  keterangan: string
  tanggalInput: string
}

export default function DataLahanPage() {
  const [formData, setFormData] = useState({
    namaLahan: "",
    luasLahan: "",
    jenisTanah: "",
    lokasi: "",
    ketinggian: "",
    kemiringan: "",
    irigasi: "",
    keterangan: ""
  })
  const [dataLahan, setDataLahan] = useState<LahanData[]>([])
  const [error, setError] = useState("")
  const [success, setSuccess] = useState("")
  const [isEditing, setIsEditing] = useState<string | null>(null)

  useEffect(() => {
    // Load data from localStorage
    const savedData = localStorage.getItem("dataLahan")
    if (savedData) {
      setDataLahan(JSON.parse(savedData))
    }
  }, [])

  const saveToLocalStorage = (data: LahanData[]) => {
    localStorage.setItem("dataLahan", JSON.stringify(data))
  }

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault()
    setError("")
    setSuccess("")

    // Validation
    if (!formData.namaLahan || !formData.luasLahan || !formData.jenisTanah || !formData.lokasi) {
      setError("Nama lahan, luas lahan, jenis tanah, dan lokasi harus diisi")
      return
    }

    if (parseFloat(formData.luasLahan) <= 0) {
      setError("Luas lahan harus lebih dari 0")
      return
    }

    const newData: LahanData = {
      id: isEditing || Date.now().toString(),
      namaLahan: formData.namaLahan,
      luasLahan: parseFloat(formData.luasLahan),
      jenisTanah: formData.jenisTanah,
      lokasi: formData.lokasi,
      ketinggian: parseFloat(formData.ketinggian) || 0,
      kemiringan: formData.kemiringan,
      irigasi: formData.irigasi,
      keterangan: formData.keterangan,
      tanggalInput: isEditing ? 
        dataLahan.find(d => d.id === isEditing)?.tanggalInput || new Date().toISOString() :
        new Date().toISOString()
    }

    let updatedData: LahanData[]
    if (isEditing) {
      updatedData = dataLahan.map(item => item.id === isEditing ? newData : item)
      setSuccess("Data lahan berhasil diperbarui")
    } else {
      updatedData = [...dataLahan, newData]
      setSuccess("Data lahan berhasil ditambahkan")
    }

    setDataLahan(updatedData)
    saveToLocalStorage(updatedData)
    resetForm()
  }

  const resetForm = () => {
    setFormData({
      namaLahan: "",
      luasLahan: "",
      jenisTanah: "",
      lokasi: "",
      ketinggian: "",
      kemiringan: "",
      irigasi: "",
      keterangan: ""
    })
    setIsEditing(null)
  }

  const handleEdit = (data: LahanData) => {
    setFormData({
      namaLahan: data.namaLahan,
      luasLahan: data.luasLahan.toString(),
      jenisTanah: data.jenisTanah,
      lokasi: data.lokasi,
      ketinggian: data.ketinggian.toString(),
      kemiringan: data.kemiringan,
      irigasi: data.irigasi,
      keterangan: data.keterangan
    })
    setIsEditing(data.id)
    setError("")
    setSuccess("")
  }

  const handleDelete = (id: string) => {
    if (confirm("Apakah Anda yakin ingin menghapus data ini?")) {
      const updatedData = dataLahan.filter(item => item.id !== id)
      setDataLahan(updatedData)
      saveToLocalStorage(updatedData)
      setSuccess("Data lahan berhasil dihapus")
    }
  }

  return (
    <div className="space-y-6">
      <div>
        <h1 className="text-3xl font-bold">Data Lahan</h1>
        <p className="text-gray-600">Kelola informasi lahan pertanian Anda</p>
      </div>

      {/* Form Input */}
      <Card>
        <CardHeader>
          <CardTitle>{isEditing ? "Edit Data Lahan" : "Tambah Data Lahan"}</CardTitle>
          <CardDescription>
            Masukkan informasi detail tentang lahan pertanian
          </CardDescription>
        </CardHeader>
        <CardContent>
          <form onSubmit={handleSubmit} className="space-y-4">
            {error && (
              <Alert variant="destructive">
                <AlertDescription>{error}</AlertDescription>
              </Alert>
            )}
            {success && (
              <Alert>
                <AlertDescription>{success}</AlertDescription>
              </Alert>
            )}

            <div className="grid md:grid-cols-2 gap-4">
              <div className="space-y-2">
                <Label htmlFor="namaLahan">Nama Lahan *</Label>
                <Input
                  id="namaLahan"
                  value={formData.namaLahan}
                  onChange={(e) => setFormData({...formData, namaLahan: e.target.value})}
                  placeholder="Contoh: Sawah Belakang Rumah"
                  required
                />
              </div>

              <div className="space-y-2">
                <Label htmlFor="luasLahan">Luas Lahan (hektar) *</Label>
                <Input
                  id="luasLahan"
                  type="number"
                  step="0.01"
                  value={formData.luasLahan}
                  onChange={(e) => setFormData({...formData, luasLahan: e.target.value})}
                  placeholder="Contoh: 2.5"
                  required
                />
              </div>

              <div className="space-y-2">
                <Label htmlFor="jenisTanah">Jenis Tanah *</Label>
                <Select value={formData.jenisTanah} onValueChange={(value) => setFormData({...formData, jenisTanah: value})}>
                  <SelectTrigger>
                    <SelectValue placeholder="Pilih jenis tanah" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="alluvial">Alluvial</SelectItem>
                    <SelectItem value="latosol">Latosol</SelectItem>
                    <SelectItem value="podsolik">Podsolik</SelectItem>
                    <SelectItem value="regosol">Regosol</SelectItem>
                    <SelectItem value="andosol">Andosol</SelectItem>
                    <SelectItem value="grumusol">Grumusol</SelectItem>
                    <SelectItem value="lainnya">Lainnya</SelectItem>
                  </SelectContent>
                </Select>
              </div>

              <div className="space-y-2">
                <Label htmlFor="lokasi">Lokasi *</Label>
                <Input
                  id="lokasi"
                  value={formData.lokasi}
                  onChange={(e) => setFormData({...formData, lokasi: e.target.value})}
                  placeholder="Contoh: Desa Sukamaju, Kec. Cianjur"
                  required
                />
              </div>

              <div className="space-y-2">
                <Label htmlFor="ketinggian">Ketinggian (mdpl)</Label>
                <Input
                  id="ketinggian"
                  type="number"
                  value={formData.ketinggian}
                  onChange={(e) => setFormData({...formData, ketinggian: e.target.value})}
                  placeholder="Contoh: 500"
                />
              </div>

              <div className="space-y-2">
                <Label htmlFor="kemiringan">Kemiringan Lahan</Label>
                <Select value={formData.kemiringan} onValueChange={(value) => setFormData({...formData, kemiringan: value})}>
                  <SelectTrigger>
                    <SelectValue placeholder="Pilih kemiringan" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="datar">Datar (0-3%)</SelectItem>
                    <SelectItem value="landai">Landai (3-8%)</SelectItem>
                    <SelectItem value="miring">Miring (8-15%)</SelectItem>
                    <SelectItem value="curam">Curam (15-25%)</SelectItem>
                    <SelectItem value="sangat-curam">Sangat Curam (lebih dari 25%)</SelectItem>
                  </SelectContent>
                </Select>
              </div>

              <div className="space-y-2">
                <Label htmlFor="irigasi">Sistem Irigasi</Label>
                <Select value={formData.irigasi} onValueChange={(value) => setFormData({...formData, irigasi: value})}>
                  <SelectTrigger>
                    <SelectValue placeholder="Pilih sistem irigasi" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="teknis">Irigasi Teknis</SelectItem>
                    <SelectItem value="semi-teknis">Semi Teknis</SelectItem>
                    <SelectItem value="sederhana">Sederhana</SelectItem>
                    <SelectItem value="tadah-hujan">Tadah Hujan</SelectItem>
                    <SelectItem value="pompa">Pompa Air</SelectItem>
                  </SelectContent>
                </Select>
              </div>
            </div>

            <div className="space-y-2">
              <Label htmlFor="keterangan">Keterangan Tambahan</Label>
              <Textarea
                id="keterangan"
                value={formData.keterangan}
                onChange={(e) => setFormData({...formData, keterangan: e.target.value})}
                placeholder="Informasi tambahan tentang lahan..."
                rows={3}
              />
            </div>

            <div className="flex gap-2">
              <Button type="submit">
                {isEditing ? "Perbarui Data" : "Simpan Data"}
              </Button>
              {isEditing && (
                <Button type="button" variant="outline" onClick={resetForm}>
                  Batal
                </Button>
              )}
            </div>
          </form>
        </CardContent>
      </Card>

      {/* Data Table */}
      <Card>
        <CardHeader>
          <CardTitle>Daftar Data Lahan</CardTitle>
          <CardDescription>
            Data lahan yang telah tersimpan dalam sistem
          </CardDescription>
        </CardHeader>
        <CardContent>
          {dataLahan.length === 0 ? (
            <div className="text-center py-8 text-gray-500">
              <p>Belum ada data lahan.</p>
              <p className="text-sm">Tambahkan data lahan pertama Anda menggunakan form di atas.</p>
            </div>
          ) : (
            <div className="overflow-x-auto">
              <Table>
                <TableHeader>
                  <TableRow>
                    <TableHead>Nama Lahan</TableHead>
                    <TableHead>Luas (ha)</TableHead>
                    <TableHead>Jenis Tanah</TableHead>
                    <TableHead>Lokasi</TableHead>
                    <TableHead>Ketinggian</TableHead>
                    <TableHead>Aksi</TableHead>
                  </TableRow>
                </TableHeader>
                <TableBody>
                  {dataLahan.map((data) => (
                    <TableRow key={data.id}>
                      <TableCell className="font-medium">{data.namaLahan}</TableCell>
                      <TableCell>{data.luasLahan}</TableCell>
                      <TableCell className="capitalize">{data.jenisTanah}</TableCell>
                      <TableCell>{data.lokasi}</TableCell>
                      <TableCell>{data.ketinggian} mdpl</TableCell>
                      <TableCell>
                        <div className="flex gap-2">
                          <Button
                            size="sm"
                            variant="outline"
                            onClick={() => handleEdit(data)}
                          >
                            Edit
                          </Button>
                          <Button
                            size="sm"
                            variant="destructive"
                            onClick={() => handleDelete(data.id)}
                          >
                            Hapus
                          </Button>
                        </div>
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </div>
          )}
        </CardContent>
      </Card>
    </div>
  )
}
